import {wxlogin,send,register} from '../../network/profile.js';
export default {
	data() {
		return {
			tel:'',
			msg:'',
			flag:'',
			unionid:''
		};
	},methods:{
		send(){
			send(this.tel).then(res=>{
				console.log(res)
				if(res.data.code == 100 ){
					this.flag = res.data.info.validateCode
				}
			})
		},
		getUser(){
			let code=''
			let encryptedData = ''
			let iv=''
			uni.login({
				success:res=> {
					 code = res.code
					 uni.getUserInfo({
						success:data=>{
							encryptedData = data.encryptedData
							iv = data.iv
							wxlogin(encryptedData,iv,code).then(res=>{
								if(res.data.msg== "解密成功"){
									register(this.tel,res.data.userInfo.unionId).then(res=>{
										console.log(res)
										if(res.data.code == 100 ){
											uni.setStorageSync('unionid',res.data.userInfo.unionId)
											uni.setStorageSync('login',true)
						
											uni.showToast({
												title:'注册成功',
												duration: 2000,
											})
											uni.navigateTo({
												url:'/pages/profile/profile'
											})
											
										}
									})
								}
								
							})
						},
						fail() {
							uni.showToast({
								title:'未知错误',
								duration: 2000
							})
						}		
					})
				}
			})	
		},
		sign(){
			if(this.flag == this.msg){
				this.getUser()
			}else{
				uni.showToast({
					title:'验证码错误'
				})
			}
		}
	}
}
